.mf-department-member {
  padding-top: 20px;
}
.mf-department-member .mf-department-member__title {
  height: 48px;
  background: #899EE3;
  border-radius: 2px 2px 0px 0px;
  line-height: 48px;
  color: #FFFFFF;
  padding: 0px 10px;
}
.mf-department-member .mf-department-member__title__name {
  font-size: 20px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-department-member .mf-department-member__add {
  font-size: 14px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-department-member .mf-department-member__add .iconfont {
  margin-right: 3px;
  font-size: 14px;
}
.mf-department-member .mf-department-member__sort {
  font-size: 14px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-department-member .mf-department-member__sort .iconfont {
  margin-right: 3px;
  font-size: 16px;
}
