.mf-orgdrawer {
  height: 100%;
  width: 0px;
  background: #EFEFEF;
  position: relative;
  padding: 30px 0px;
  box-sizing: border-box;
}
.mf-orgdrawer .mf-orgdrawer__content {
  display: none;
}
.mf-orgdrawer .mf-orgdrawer__title {
  margin-bottom: 0px;
  font-size: 16px;
  color: #333333;
  font-weight: bold;
}
.mf-orgdrawer.show {
  width: 320px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.mf-orgdrawer.show .mf-orgdrawer__content {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.mf-orgdrawer .mf-orgdrawer__collapse {
  color: #333333;
  font-size: 14px;
  padding: 10px 5px;
  width: 120px;
}
.mf-orgdrawer .mf-orgdrawer__collapse .iconxiangzuo {
  margin-right: 5px;
  font-size: 12px;
}
.mf-orgdrawer .mf-orgdrawer__expand {
  width: 30px;
  height: 60px;
  display: none;
  position: absolute;
  left: 0px;
  top: 50%;
  margin-top: -30px;
  justify-content: center;
  align-items: center;
  background: url(data:image/png;base64,);
}
.mf-orgdrawer .mf-orgdrawer__expand.show {
  display: flex;
}
.mf-orgdrawer .mf-orgdrawer__expand .iconxiangyou {
  font-size: 24px;
}
