.edit-organize {
  padding: 60px 20px;
}
.edit-organize .edit-organize__title {
  font-size: 20px;
  color: #333333;
  font-weight: bold;
  margin-bottom: 40px;
}
.edit-organize .ant-input-lg {
  font-size: 14px;
}
.edit-organize .btn--width {
  width: 120px;
}
.edit-organize .edit-organize__form__operate button {
  margin-right: 20px;
}
.edit-organize .edit-organize__form__upperorg {
  display: flex;
  background: #FFFFFF;
  border: 1px solid #E0E0E0;
  height: 48px;
  align-items: center;
  color: #333;
  border-radius: 4px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.edit-organize .edit-organize__form__upperorg .edit-organize__upperorg__left {
  flex: 1 1;
  text-align: left;
  padding-left: 20px;
}
.edit-organize .edit-organize__form__upperorg .edit-organize__upperorg__right {
  flex: 1 1;
  text-align: right;
  padding-right: 20px;
}
.edit-organize .edit-organize__form__upperorg:hover {
  border: 1px solid #8fb0ff;
  cursor: pointer;
}
.edit-organize .edit-organize__form__upperorg:hover .iconfont {
  color: #8fb0ff;
}
.eidt-organize-modal__mftree .eidt-organize-modal__mftree__container {
  padding: 20px;
  background: #EFEFEF;
}
.eidt-organize-modal__mftree .eidt-organize-modal__mftree__status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.eidt-organize-modal__mftree .eidt-organize-modal__mftree__status .eidt-organize-modal__status {
  padding: 20px 0px 20px 0px;
  width: 100%;
}
