.mf-child-department {
  padding-top: 20px;
}
.mf-child-department .mf-child-department__title {
  height: 48px;
  background: #899EE3;
  border-radius: 2px 2px 0px 0px;
  line-height: 48px;
  color: #FFFFFF;
  padding: 0px 10px;
}
.mf-child-department .mf-child-department__title__name {
  font-size: 20px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-child-department .mf-child-department__add {
  font-size: 14px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-child-department .mf-child-department__add .iconfont {
  margin-right: 3px;
  font-size: 14px;
}
.mf-child-department .mf-child-department__sort {
  font-size: 14px;
  color: #FFFFFF;
  margin-right: 40px;
}
.mf-child-department .mf-child-department__sort .iconfont {
  margin-right: 3px;
  font-size: 16px;
}
.mf-child-department .mf-child-department__content {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.mf-child-department .mf-child-department__content .mf-child-department__content__item {
  width: 25%;
  padding: 10px 0px;
}
