.mf-department-header {
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 115px;
  background: #F6F6F6;
  border-radius: 2px;
}
.mf-department-header .mf-department-header__title {
  font-size: 20px;
  color: #333333;
  flex: 1 1;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 0px;
}
.mf-department-header .mf-department-header__title .mf-department-header__title__review {
  font-size: 14px;
  color: #6186FF;
  margin-left: 20px;
  cursor: pointer;
}
.mf-department-header .mf-department-header__title .iconfont {
  font-size: 20px;
  color: #6186FF;
  margin-right: 5px;
}
.mf-department-header .mf-department-header__search {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 50px;
  margin-top: -20px;
}
.mf-department-header .mf-department-header__search .mf-department-header__search__xx {
  flex: 1 1;
  font-size: 14px;
  color: #666666;
}
.mf-department-header .mf-department-header__search .mf-department-header__search__wrap {
  width: 300px;
  margin-right: 20px;
}
.mf-department-header .mf-department-header__search .ant-input-affix-wrapper .ant-input {
  font-size: 12px;
}
.mf-department-header .mf-department-header__edit {
  margin-left: 10px;
}
