.mf-tree {
  padding-top: 30px;
}
.mf-tree .ant-tree li span.ant-tree-iconEle {
  display: none;
}
.mf-tree .ant-tree li span.ant-tree-switcher .ant-tree-switcher-icon {
  font-size: 16px;
}
.mf-tree .ant-tree-title {
  font-size: 14px;
}
.mf-tree .ant-tree li {
  padding: 0px 5px 0px 0px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mf-tree .ant-tree li .ant-tree-node-content-wrapper {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mf-tree .ant-tree li .ant-tree-node-content-wrapper {
  color: #333;
}
.mf-tree .ant-tree li span.ant-tree-switcher.ant-tree-switcher_open .ant-tree-switcher-icon,
.mf-tree :root .ant-tree li span.ant-tree-switcher.ant-tree-switcher_open .ant-select-switcher-icon {
  font-size: 18px;
}
.mf-tree .ant-tree li span.ant-tree-switcher.ant-tree-switcher_close .ant-tree-switcher-icon,
.mf-tree :root .ant-tree li span.ant-tree-switcher.ant-tree-switcher_close .ant-select-switcher-icon {
  font-size: 18px;
}
